.App {
  font-family: Arial, Helvetica, sans-serif;
}
.App-header {
  display: flex;
  align-items: center;
  border-bottom: 3px solid #c1384c;
  color: #c1384c;
}

.App-body {
  font-size: 1rem;
  padding: 50px;
}

.App-footer {
  border-top: 3px solid #c1384c;
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  font-style: italic;
}
.App-logo{
  width: 200px;
  height: auto;
}



